import React, { Component } from "react";
import { Router, Route, Switch } from "react-router-dom";
import NotFound from "./Pages/NotFound/NotFound";
import "./App.css";
import asyncComponent from "./AsyncComponent";

import Header from "./Container/Header";
import Footer from "./Container/Footer";

var createBrowserHistory = require("history").createBrowserHistory;

const Home = asyncComponent(() =>
    import("./Pages/Home/Home").then((module) => module.default)
);

const HowToScript = asyncComponent(() =>
    import("./Pages/HowToScript/index").then((module) => module.default)
);

// const Maps = asyncComponent(() =>
//     import("./Pages/Maps/Maps").then((module) => module.default)
// );

// const Blog = asyncComponent(() =>
//     import("./Pages/Blog/Blog").then((module) => module.default)
// );

const history = createBrowserHistory();

class App extends Component {
    render() {
        return (
            <Router history={history}>
                <div>
                    <Header />
                    <section className="content">
                        <Switch>
                            <Route exact path="/" component={Home} />
                            <Route exact path="/how-to-script" component={HowToScript} />
                            {/* <Route path="/maps" component={Maps} />
                            <Route path="/blog" component={Blog} /> */}
                            <Route path="*" component={NotFound} />
                        </Switch>
                    </section>
                <Footer />

                </div>
            </Router>
        );
    }
}

export default App;
